import Vue from 'vue'
import CscSingleTable from '@/components/CscSingleTable/CscSingleTable'// 
import CscMasterTable from '@/components/CscMasterTable/CscMasterTable'// 
import CscFormPage from '@/components/CscFormPage/CscFormPage'// 
import CscMasterFormPage from '@/components/CscMasterFormPage/CscMasterFormPage'// 
import CscDate from '@/components/CscDate/CscDate'// 
import Ueditor from '@/components/Ueditor/Ueditor' // 
import CscBaseRef from '@/components/CscRef/CscBaseRef' // 
import CscFormDialog from "@/components/CscFormDialog/CscFormDialog" // 

// register component globally
Vue.component('csc-single-table', CscSingleTable)
Vue.component('csc-master-table', CscMasterTable)
Vue.component('csc-form-page', CscFormPage)
Vue.component('csc-master-form-page', CscMasterFormPage)
Vue.component('csc-base-ref', CscBaseRef)
Vue.component('csc-form-dialog', CscFormDialog)
Vue.component('csc-date', CscDate)
Vue.component('ueditor', Ueditor)




