import axios from 'axios'
import { Message, MessageBox } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'
import qs from 'qs'

// axios
const service = axios.create({
  // baseURL: process.env.BASE_API, // apibase_url
  baseURL: "/", // apibase_url
  // baseURL: "/api/remote", //p2sapi
  timeout: 15000                  // 
})

// request
service.interceptors.request.use(config => {
  if (store.getters.token) {
    config.headers['X-Token'] = getToken() // token 
  }

  if(typeof(config.data) == "undefined"){
    config.data={}
  }
  //P2S
  if(typeof(config.entity) != "undefined"){
    config.data.msgEntity=config.entity
  }
  config.data.msgHead={}
  //requestcommon
  config.data.msgCommon={"username":store.getters.name,'stff_id': store.getters.userId}
   // P2S
  //P2S

   //origin reqBody
   // var msgData={};
   // if(typeof(config.entity) != "undefined"){
   // msgData.msgEntity=config.entity
   // }
   // msgData.msgCommon = {"username":store.getters.name,'stff_id':"USER2017121300000001"}
   // msgData.msgHead = {}
   //
   //
   // //requestBody
   // var data = {};
   // data.serviceId='restService'
   // data.serviceParam=JSON.stringify({"url":config.url})
   // data.jsonData=JSON.stringify(msgData)
   // // data.serviceParam={"url":config.url}
   // // data.jsonData=msgData
   //
   // //url
   // config.url=''
   // config.data = qs.stringify(data)
  //P2S


  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(
  response => {
    // loadingfalse
    if (store.getters.listLoading) {
      store.dispatch('setListLoading', false)
    }
    if (store.getters.childLoading) {
      store.dispatch('setChildLoading', false)
    }
    /**
     * code00
     */
    const res = response.data
    var msgHead=res.msgHead;
    if(msgHead != undefined && msgHead.sys_tx_status != '00'){
      Message({
        message: "["+msgHead.sys_resp_code+"]"+msgHead.sys_resp_desc,
        type: 'error',
        duration: 5 * 1000
      })
      return Promise.reject('error')
    }else{
      return response.data.msgEntity
    }
    // if(res.code != '00'){
    //   Message({
    //     message: res.errorMsg,
    //     type: 'error',
    //     duration: 5 * 1000
    //   })
    //   return Promise.reject('error')
    // }
    // if (res.code !== 20000) {
    //   Message({
    //     // message: res.data,
    //     message: res.error,
    //     type: 'error',
    //     duration: 5 * 1000
    //   })
    //
    //   // 50008:token; 50012:;  50014:Token ;
    //   if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
    //     MessageBox.confirm('', '', {
    //       confirmButtonText: '',
    //       cancelButtonText: '',
    //       type: 'warning'
    //     }).then(() => {
    //       store.dispatch('FedLogOut').then(() => {
    //         location.reload()// vue-router bug
    //       })
    //     })
    //   }
    //   return Promise.reject('error')
    // } else {
    //   return response.data
    // }
    // return response.data
  },
  error => {
    console.log('err' + error)// for debug
    Message({
      message: "",
      type: 'error',
      duration: 5 * 1000
    })
    // loadingfalse
    if (store.getters.listLoading) {
      store.dispatch('setListLoading', false)
    }
    if (store.getters.childLoading) {
      store.dispatch('setChildLoading', false)
    }
    return Promise.reject(error)
  }
)

export default service



